<template>
<div id="app">
<img src="~assets/images/logo.png" width="52"/>
  <div class="box">
  	<h3>detail</h3>
    <div>{{userName}}</div>
    <div>{{age}}</div>
    <router-link :to="{name:'detail'}" >detail</router-link>
  	</div>
  </div>

</div>
</template>

<script>
export default {
  data () {
    return {
      userName:'',
      age:''
    }
  },
  created () {
    var that = this;
    this.axios.get('http://localhost:3002/api/home/list', {
          params: {
            ID: 12345
          }
        })
        .then(function (response) {
          console.log(response.data.data)
          that.userName = response.data.data.username;
          that.age = response.data.data.age;
        })
        .catch(function (error) {
          console.log(error);
        });
  },
  mounted () {

  },
  methods:{
     
   }
}
</script>



